<script>
  import { onMount } from "svelte";
  import { slide } from "svelte/transition";

  import {
    Theme,
    Slider,
    Checkbox,
    RadioButtonGroup,
    RadioButton,
  } from "carbon-components-svelte";
  import "carbon-components-svelte/css/all.css";

  let theme = "g10";
  $: document.documentElement.setAttribute("theme", theme);

  import Gallery from "svelte-brick-gallery";
  import Image from "svelte-brick-gallery/Image.svelte";

  const version = "0.0.5";

  /**
   * @type {string | any[]}
   */
  let images = [];
  let itemHeight = 150;
  let gap = 0;

  let height = 120;
  let pad = 10;
  let justify = "left";

  const scale = (w, h) => {
    return Math.floor((height * w) / h);
  };

  let page = 1;
  const getImages = async () => {
    let limit = 10;
    if (page === 1) {
      limit = 30;
    }
    let res = await fetch(
      `https://picsum.photos/v2/list?page=${page}&limit=${limit}`
    );

    images = images.concat(
      (await res.json()).map((img) => ({
        src: `https://picsum.photos/id/${img.id}/${
          scale(img.width, img.height) + pad
        }/${height + pad}`,
        id: img.id,
        width: img.width,
        height: img.height,
      }))
    );

    if (limit === 30) {
      page = 3;
    }
    page++;

    return images;
  };

  const load = getImages();

  let scroll;
  let winHeight = 0;
  let bodyHeight = 0;

  let viewedSrc = "";
  let viewed = false;

  const setViewed = (id, width, height) => {
    viewedSrc = `https://picsum.photos/id/${id}/${width}/${height}`;
    viewed = true;
  };

  let infiniteLoad = true;
  let roundBorder = false;
  let hoverEffect = false;
  let animated = false;

  $: if (scroll > 0 && winHeight > 0) {
    if (Math.ceil(scroll + winHeight) >= bodyHeight) {
      console.log("bot");
      if (infiniteLoad) {
        getImages();
      }
    }
  }

  let currentTheme = "dark";

  const setTheme = (theme) => {
    document.body.classList.remove(currentTheme);
    currentTheme = theme;
    document.body.classList.add(currentTheme);
  };

  window
    .matchMedia("(prefers-color-scheme: dark)")
    .addEventListener("change", (event) => {
      console.log("bt");
      setTheme(event.matches ? "dark" : "light");
    });

  $: setTheme(currentTheme);

  onMount(() => {
    if (window.matchMedia) {
      if (window.matchMedia("(prefers-color-scheme: light)").matches) {
        currentTheme = "light";
      }
    }
  });

  function animate(node, options) {
    if (animated) {
      return options.animation(node, options);
    }
  }
</script>

<svelte:window bind:innerHeight={winHeight} bind:scrollY={scroll} />

<div class="flex txt-default overflow-x-clip" bind:clientHeight={bodyHeight}>
  <div class="w-1/3">
    <div class="h-screen w-1/3 fixed flex flex-col">
      <div class="flex-1 p-4">
        <div class="p-4 mb-4 ">
          <h2 class="text-xl">Props</h2>
          <div class="p-2">
            <Slider labelText="Gap (px)" min={0} max={48} bind:value={gap} />
          </div>
          <div class="p-2">
            <Slider
              labelText="itemHeight (px)"
              min={100}
              max={500}
              step={10}
              bind:value={itemHeight}
            />
          </div>
          <div class="p-2">
            <RadioButtonGroup legendText="Justify" bind:selected={justify}>
              <RadioButton labelText="left" value="left" />
              <RadioButton labelText="center" value="center" />
              <RadioButton labelText="right" value="right" />
            </RadioButtonGroup>
          </div>
        </div>
        <div class="p-4">
          <h2 class="text-lg">Things you can easily add yourself</h2>
          <div>
            <Checkbox labelText="Border radius" bind:checked={roundBorder} />
          </div>
          <div>
            <Checkbox labelText="Hover effect" bind:checked={hoverEffect} />
          </div>
          <div>
            <Checkbox labelText="Transition" bind:checked={animated} />
          </div>
          <div>
            <Checkbox
              labelText="Infinite loading"
              bind:checked={infiniteLoad}
            />
          </div>
        </div>
      </div>
      <div>
        <Theme
          render="toggle"
          toggle={{
            themes: ["g10", "g90"],
            labelA: "Enable dark mode",
            labelB: "disable dark mode",
            hideLabel: true,
            size: "sm",
          }}
        />
        <span>demo {version}</span>
      </div>
    </div>
  </div>
  <div class="flex-1 overflow-x-visible">
    {#await load}
      loading...
    {:then}
      <div class={gap === 0 ? "p-0" : "p-4"}>
        <Gallery {images} {justify} {itemHeight} itemWidth={500} {gap}>
          <div
            slot="image"
            let:index
            let:style
            style="height:100%; position:relative;"
          >
            <img
              transition:animate={{ animation: slide, duration: 325 }}
              src={images[index].src}
              style={style + (roundBorder ? " border-radius: 12px;" : "")}
              class={hoverEffect ? "hover:scale-105" : ""}
              alt={index}
              on:click={() =>
                setViewed(
                  images[index].id,
                  images[index].width,
                  images[index].height
                )}
            />
          </div>
        </Gallery>
      </div>
    {:catch}
      loading error
    {/await}
  </div>
</div>

{#if viewed}
  <div
    class="fixed z-10 top-0 p-10  h-screen w-full bg-color-1/75 backdrop-blur-md"
    on:click={() => (viewed = false)}
  >
    <div class="h-full">
      <Image src={viewedSrc} classes="w-full h-full object-contain" />
    </div>
  </div>
{/if}

