  @tailwind base;
  @tailwind components;
  @tailwind utilities;

  html,
  body {
    margin: 0;
    padding: 0;
  }

  body {
    @apply bg-color-1;
  }

  :root {
    --color-1: black;
    --color-2: black;
    --color-3: black;
    --color-4: black;
    --text-default: black;
    --text-secondary: black;
  }

  .dark {
    --color-1: #1e1e1e;
    --color-1-75: #1e1e1ebf;
    --color-2: #272727;
    --color-3: #353535;
    --color-4: #505050;
    --text-default: #848484;
    --text-secondary: #505050;
  }

  .light {
    --color-1: #fdf6e3;
    --color-1-75: #fdf6e3bf;
    --color-2: #dbd5c1;
    --color-3: #c9c3b3;
    --color-4: #beb7a5;
    --text-default: #6e6d6b;
    --text-secondary: #a79e83;
  }

  .bg-color-1 {
    background-color: var(--color-1);
  }
  .bg-color-1\/75 {
    background-color: var(--color-1-75);
  }
  .bg-color-2 {
    background-color: var(--color-2);
  }
  .bg-color-3 {
    background-color: var(--color-3);
  }
  .bg-color-4 {
    background-color: var(--color-4);
  }
  .txt-default {
    color: var(--text-default);
  }
  .txt-secondary {
    color: var(--text-secondary);
  }
</style>
